@import "../../styles/style.css";
@import '../../styles/model.css';
@import '../../styles/animate.css';

/* 地图 */
.map[data-v-aae98fa0]{
  width:100%;
  height:600px;
  position: relative;
  background: url(../../../static/view.jpg) no-repeat center;
  background-size: 100%;
}
.map-bg-img[data-v-aae98fa0]{    /* 背景图片 */
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.map-bg-gary[data-v-aae98fa0]{   /* 背景灰色蒙版 */
  background: rgba(24,24,24,0.7);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.map-img-1[data-v-aae98fa0]{   /* 地图全局图片和热点选区 包裹，距顶部50px */
  position: absolute;
  width: 50%;
  height: 90%;
  top: 50px;
  left: 0;
  z-index:1000;
}
.map-img-2[data-v-aae98fa0]{   /* 地图全局图片和热点选区 距右边100px */
  margin:0 100px;
  width:605px;
  height:450px;
}
.map-areas[data-v-aae98fa0]{   /* 地图局部 */
  position: absolute;
  width: 100%;
  height: 90%;
  top: 50px;
  left: 0;
  z-index: 90;
}
.map-areas img[data-v-aae98fa0]{   /* 地图局部图片 */
  position: absolute;
  left: 100px;
  width:605px;
  height:450px;
}
.area[data-v-aae98fa0]{    /* 地图热点样式 */
  outline:none;
}
.map-point[data-v-aae98fa0]{   /* 地图村落点 */
  position: absolute;
  width: 30px;
  height: 30px;
  z-index: 2000;
  background: red;
  cursor:pointer;
  background: url(../../../static/zhuye/point.png) no-repeat center center;
  background-size: 30px
}
.mapP-active[data-v-aae98fa0]{
  background: url(../../../static/zhuye/point2.gif) no-repeat center center;
}
.map-areaName[data-v-aae98fa0]{    /* 地图乡镇名字 */
  position: absolute;
  z-index: 1005;
  color: #999;
  font-size: 14px;
}
.map-texts[data-v-aae98fa0]{
  position: absolute;
  width: 30%;
  height: 200px;
  z-index: 90;
  top:40%;
  right: 15%;
  transform: translateY(-50%);
  text-align: center;
}
.map-texts a h3[data-v-aae98fa0]{
  color: #ffffff;
  font-size: 40px;
  line-height: 50px;
  height: 50px;
  display: block;
  width: 100%;
}
.map-texts a div[data-v-aae98fa0]{
  color: #ffffff;
  margin-top: 20px;
  font-size: 16px;
  line-height: 25px;
  /* height: 100px; */
  display: block;
  width: 100%;
}

/* 标语与搜索 */
.search-words-box[data-v-aae98fa0]{
  height: 130px;
  width: 100%;
  background: #efefef;
}
.search-words-box-center[data-v-aae98fa0]{
  width: 1000px;
  height: 130px;
  margin: 0px auto;
  /* background: salmon; */
}
.topic-words[data-v-aae98fa0]{
  float: left;
  width: 449px;
  height: 130px;
  /* background: seagreen; */
  line-height: 130px;
  font-size: 18px;
  text-align: right;
  padding-right: 50px;
  color: #797979;
}
.box-right[data-v-aae98fa0]{
  /* background: seagreen; */
  width: 499px;
  height: 60px;
  margin-top: 35px;
  padding-top: 8px;
  float: right;
  box-sizing: border-box;
  border-left: 1px solid #d6d6d6;
}
.search-box[data-v-aae98fa0]{
  height: 44px;
  width: 300px;
  /* background: sandybrown; */
  margin: 0px 0px 0px 50px;
}
.search-input[data-v-aae98fa0]{
  height: 38px;
  border: 1px solid #efefef;
  width: 220px;
  background: #fff;
  float: left;
  outline: none;
}
.search-icon[data-v-aae98fa0]{
  height: 44px;
  width: 44px;
  float: left;
  background: #FFDE3A;
}
.search-icon img[data-v-aae98fa0]{
  margin: 3px;
}

/* 特别推荐和关于我们的标题 */
.recom-title[data-v-aae98fa0],.aboutOur-title[data-v-aae98fa0]{
  width: 100%;
  height: 90px;
  text-align: center;
  position: relative;
  box-sizing: border-box;
  z-index: 1;
}
.recom-title .div1[data-v-aae98fa0],.aboutOur-title .div1[data-v-aae98fa0]{
  display: block;
  width: 180px;
  height: 90px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: #fff;
  z-index: 2;
}
.recom-title .div2[data-v-aae98fa0],.aboutOur-title .div2[data-v-aae98fa0]{
  width: 100%;
  height: 45px;
  position: absolute;
  top: 45px;
  left: 0px;
  background: #fff;
  border-top: 2px solid #bfbfbf;
  z-index: 1;
}
.recom-title span[data-v-aae98fa0],.aboutOur-title span[data-v-aae98fa0]{
  display: block;
  height: 90px;
  width: 120px;
  line-height: 90px;
  font-size: 24px;
  text-align-last: justify;
  margin: 0 auto;
}

/* 特别推荐 */
.recommend[data-v-aae98fa0]{
  /* height: 600px; */
  width: 1000px;
  margin: 50px auto;
  min-height: 400%;
  /* background: sandybrown; */
}
.recom-context[data-v-aae98fa0]{
  /* height: 500px; */
  width: 100%;
  margin-top: 30px;
  /* background: lemonchiffon; */
}
.recom-context .recom-col[data-v-aae98fa0]{
  width: 230px;
  height: 486px;
  /* background: aquamarine; */
  float: left;
  margin-right: 26px;
}
.recom-context .recom-col .margin-top-26[data-v-aae98fa0]{
  margin-top: 26px;
}
    /* 视频 */
.recom-context .recom-col .col-video[data-v-aae98fa0]{
  height: 146px;
  width: 100%;
  background: aquamarine;
  position: relative;
  cursor: pointer;
  z-index: 60;
}
.recom-context .recom-col .col-video .image-auto[data-v-aae98fa0]{
  width: 100%;
  height: 100%;
}
.recom-context .recom-col .col-video .video-black-cover[data-v-aae98fa0]{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  top: 0px;
  left: 0px;
  background: rgba(0,1,0,0.5);
}
.recom-context .recom-col .col-video .video-black-cover .play-icon[data-v-aae98fa0]{
  height: 30px;
  width: 30px;
  margin: 58px auto 0px;
  /* background: lightcoral; */
}
.recom-context .recom-col .col-video .video-black-cover .play-icon img[data-v-aae98fa0]{
  transition: All .5s ease-in-out;
}
.recom-context .recom-col .col-video:hover .video-black-cover .play-icon img[data-v-aae98fa0]{
    transform: scale(1.3);
}
.recom-context .recom-col .col-video .video-black-cover .span[data-v-aae98fa0]{
  margin: 30px 15px 0px 15px;
  overflow: hidden;
  color: #fff;
  font-size: 13px;
  display: block;
  white-space: nowrap;
}
    /* 乡村1、2 */
.recom-context .recom-col .col-country1[data-v-aae98fa0]{
  width: 100%;
  background: #efefef;
  position: relative;
  cursor: pointer;
  height: 314px;
  overflow: hidden;
  border-bottom: 5px solid #efefef;
  box-sizing: border-box;
  z-index: 60;
}
.recom-context .recom-col .col-country2[data-v-aae98fa0]{
  height: 100%;
  background: #efefef;
  cursor: pointer;
  position: relative;
  width: 100%;
  overflow: hidden;
  border-bottom: 10px solid #efefef;
  box-sizing: border-box;
}
.recom-context .recom-col .col-country1 .country-infor-contain[data-v-aae98fa0]{
  width: 100%;
  height: 125px;
  z-index: 80;
  position: absolute;
  background: #efefef;
  transition: All .5s ease-in-out;
}
.recom-context .recom-col .col-country2 .country-infor-contain[data-v-aae98fa0]{
  width: 100%;
  height: 200px;
  z-index: 80;
  position: absolute;
  bottom: 0;
  background: #efefef;
  transition: All .5s ease-in-out;
}
.recom-context .recom-col .col-country1:hover .country-infor-contain1[data-v-aae98fa0]{
    transform: translateY(-50px);
}
.recom-context .recom-col .col-country2:hover .country-infor-contain2[data-v-aae98fa0]{
    transform: translateY(-40px);
}
.recom-context .recom-col .country-infor-contain .country-name[data-v-aae98fa0]{
  display: block;
  cursor: pointer;
  font-size: 22px;
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
}
.recom-context .recom-col .country-infor-contain p[data-v-aae98fa0]{
  width: 100%;
  font-size: 14px;
  box-sizing: border-box;
  padding: 10px;
  color: #666;
}
.recom-context .recom-col .touming[data-v-aae98fa0]{
  width: 100%;
  height: 10px;
  position: absolute;
  bottom: 0px;
  z-index: 66;
  background: -webkit-linear-gradient(rgba(239,239,239,0.2), rgba(239,239,239,0.95)); /* Safari 5.1 - 6.0 */
}
    /* 图片集、线路、游记 */
.recom-context .recom-col .images-belong[data-v-aae98fa0],.route-title[data-v-aae98fa0],.tripnotes-title[data-v-aae98fa0]{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 60;
  top: 0px;
  left: 0px;
  background: rgba(0,1,0,0.2);
}
.recom-context .recom-col .col-yin[data-v-aae98fa0]{
  width: 230px;
  height: 230px;
  position: absolute;
  top: 0;
  z-index: 200;
  opacity: 0;
  transition: All .5s ease-in-out;
  background: radial-gradient(rgba(255,255,255,0),rgba(0,0,0,0.3),rgba(0,0,0,0.7));
}/* 阴影 */
.recom-context .recom-col .col-images[data-v-aae98fa0],.col-route[data-v-aae98fa0],.col-tripnotes[data-v-aae98fa0]{
  width: 100%;
  background: aquamarine;
  cursor: pointer;
  height: 230px;
  position: relative;
}
.recom-context .recom-col .col-images:hover .col-yin[data-v-aae98fa0],.col-route:hover .col-yin[data-v-aae98fa0],.col-tripnotes:hover .col-yin[data-v-aae98fa0]{
    opacity: 1;
}
.recom-context .recom-col .topic-icon-right[data-v-aae98fa0]{
  width: 54px;
  height: 25px;
  line-height: 20px;
  padding: 0px 5px;
  display: block;
  font-size: 11px;
  float: right;
  text-align: right;
  margin-top: -30px;
  z-index: 2;
  color: #fff;
  background: url(/static/label1.png) no-repeat 0px -59px;
}
.recom-context .recom-col .images-belong span[data-v-aae98fa0],.route-title span[data-v-aae98fa0]{
  display: block;
  margin: 18px 0px 0px 23px;
  color: #fff;
}
.recom-context .recom-col .tripnotes-title span[data-v-aae98fa0]{
  display: block;
  margin: 180px 23px 0px 23px;
  color: #fff;
}
.recom-context .recom-col > div[data-v-aae98fa0]{
  box-shadow: #ccc 2px 2px 3px 2px;
}

/* 关于我们 */
.aboutOur[data-v-aae98fa0]{
  min-height: 400px;
  width:1000px;
  margin: 50px auto;
}
.aboutOur-context[data-v-aae98fa0]{
  width: 900px;
  margin: 40px auto;
}
.aboutOur-col[data-v-aae98fa0]{
  /* height: 500px; */
  width: 300px;
  float: left;
  box-sizing: border-box;
  padding: 0px 25px;
}
.aboutOur-icon[data-v-aae98fa0]{
  height: 120px;
  width: 120px;
  margin: 0 auto;
  border: 4px solid #515151;
  border-radius: 50%;
}
.aboutOur-icon img[data-v-aae98fa0]{
  height: 60%;
  width: 60%;
  margin: 20%;
}
.aboutOur-text[data-v-aae98fa0]{
  width: 200px;
  height: 180px;
  line-height: 25px;
  font-size: 16px;
  /* background: lightblue; */
  margin: 40px auto;
  text-align: center;
}
.aboutOur-btn[data-v-aae98fa0]{
  width: 120px;
  height: 34px;
  border-radius: 3px;
  background: #FFDE3A;
  margin: -20px auto;
  text-align: center;
  line-height: 34px;
  font-size: 13px;
  color: #fff;
  cursor: pointer;
}

/* 底部 */
.footer[data-v-aae98fa0]{
  margin-top: 100px;
  width: 100%;
  height: 120px;
  background: #000;
  color: #fff;
  text-align: center;
  line-height: 120px;
}

/* 搜索模态框 */
.des-ul[data-v-aae98fa0]{
  width: 100%;
  line-height: 25px;
  margin-bottom: 15px;
}
.des-li[data-v-aae98fa0]{
  float: left;
  height: 30px;
  line-height: 30px;
  margin-right: 25px;
  font-size: 16px;
  cursor: pointer;
}




